import { useState } from "react";
import { Form, Row, Col, Button, Alert } from "react-bootstrap";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { actionFullLogin } from "../actions/types";

const LoginForm = ({ promise, onLogin }) => {
  const [login, setLogin] = useState("");
  const [password, setPassword] = useState("");
  const [passwordShown, setPasswordShown] = useState(false);
  return (
    <div className="AuthForm mx-auto mt-5">
      <Form>
        {promise.login &&
        promise.login.status === "RESOLVED" &&
        !promise.login.payload ? (
          <Alert>
            ,     , 
                .
          </Alert>
        ) : null}
        <h1 className="text-center">  </h1>
        <Form.Group as={Row} className="mb-3" controlId="formHorizontalEmail">
          <Form.Label column sm={2}>
            :
          </Form.Label>
          <Col sm={10}>
            <Form.Control
              type="text"
              placeholder="  "
              onChange={(e) => setLogin(e.target.value)}
            />
          </Col>
        </Form.Group>
        <Form.Group
          as={Row}
          className="mb-3"
          controlId="formHorizontalPassword"
        >
          <Form.Label column sm={2}>
            :
          </Form.Label>
          <Col sm={10}>
            <Form.Control
              type={passwordShown ? "text" : "password"}
              placeholder="  "
              onChange={(e) => setPassword(e.target.value)}
            />
            <Button
              className="mt-2"
              variant="secondary"
              onClick={() => setPasswordShown(!passwordShown)}
            >
              {`${passwordShown ? "" : ""} `}
            </Button>
          </Col>
        </Form.Group>
        <Form.Group as={Row} className="mb-3">
          <Col sm={{ span: 10, offset: 2 }} className="my-3">
            <Button
              variant="success"
              disabled={login.length < 1 || password.length < 1 ? true : false}
              onClick={() => onLogin(login, password)}
            >
              
            </Button>
          </Col>
          <Col sm={{ span: 10, offset: 2 }}>
             ?{" "}
            <Link className="btn btn-warning" to={"/signup"}>
              
            </Link>
          </Col>
        </Form.Group>
      </Form>
    </div>
  );
};

export const CLoginForm = connect((state) => ({ promise: state.promise }), {
  onLogin: actionFullLogin,
})(LoginForm);
